import React from "react"
import "./header.styles.scss"
import { Link, useStaticQuery, graphql } from "gatsby"
import {FaGithub, FaLinkedin} from "react-icons/fa"

export default function Header() {
    const data = useStaticQuery(
        graphql`
            query {
                site {
                    siteMetadata {
                        title
                    }
                }
            }
        `
    )
     
    return (
        <div className="header-main">
            <h1 className="name">{data.site.siteMetadata.title}</h1>
            <div className="social-media">
                <a href="https://www.github.com/HerreraManuel" className="icon"><FaGithub size={45}/></a>
                <a href="https://www.linkedin.com/in/herrera-manuel" className="icon"><FaLinkedin size={45}/></a>
            </div>
            <span className="links">
            <Link to="/">About Me</Link> 
            | 
            <Link to="/projects">Projects</Link> 
            {/*
            |
                <Link to="/work">Work</Link>
                */}
        </span>
        <hr />
        </div>
        )
}
